import React from "react";
import {
  BrowserRouter as Router,
  HashRouter,
  Route,
  Switch,
} from "react-router-dom";
// import pages
import Home from "./pages/Home";
import About from "./pages/About";
import Error from "./pages/Error";
// import components
import Navbar from "./components/Navbar";

import SingleProductContainer from "./components/MyProduct/Product/SingleProductContainer";
import Order from "./pages/Order";
import HeaderInfo from "./components/HeaderInfo/HeaderInfo";
import Phone from "./components/Phone/Phone";
import Footer from "./components/Footer/Footer";
import SliderCustom from "./components/Slider/SliderCustom1";
import SliderBody from "./components/Slider/SliderBody";
import NuvbarCustom from "./components/HeaderInfo/NuvbarCustom/NuvbarCustom";

function App(props) {
  return (
    <HashRouter>
      <HeaderInfo />
      <NuvbarCustom />
      <Switch>
        <Route exact path="/">
          <Home store={props.store} />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/order">
          <Order />
        </Route>
        <Route path="/work">
          <SliderBody />
        </Route>
        <Route path="/product/:id">
          <SingleProductContainer store={props.store} />
        </Route>
        <Route path="*">
          <Error />
        </Route>
      </Switch>
      <Footer />
    </HashRouter>
  );
}

export default App;
